          delete: 'delete-config-prosen-bpjs-tenaga-kerja'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'description', label: 'Keterangan', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'periode_month', label: 'Periode', methods: { list: { transform: 'period' }, detail: { transform: 'period' }, create: { type: 'month' }, update: { type: 'month' }, filter: false } },
          { id: 'upah_max_jp_value', label: 'Upah Maksimal (JP)', methods: { list: { transform: 'money' }, detail: { transform: 'money' }, create: { type: 'money', attr: { precision: 2 } }, update: { type: 'money', attr: { precision: 2 } }, filter: false } },
