          { id: 'prosen_jkm', label: 'JKM (%)', methods: { list: { transform: 'prosen' }, detail: { transform: 'prosen' }, create: { type: 'number', attr: { precision: 2 } }, update: { type: 'number', attr: { precision: 2 } } } },
          { id: 'prosen_jp_bb_company', label: 'JP BB Perusahaan (%)', methods: { list: { transform: 'prosen' }, detail: { transform: 'prosen' }, create: { type: 'number', attr: { precision: 2 } }, update: { type: 'number', attr: { precision: 2 } } } },
          { id: 'prosen_jp_bb_employee', label: 'JP BB Karyawan (%)', methods: { list: { transform: 'prosen' }, detail: { transform: 'prosen' }, create: { type: 'number', attr: { precision: 2 } }, update: { type: 'number', attr: { precision: 2 } } } },
          { id: 'prosen_jht_company', label: 'JHT Perusahaan (%)', methods: { list: { transform: 'prosen' }, detail: { transform: 'prosen' }, create: { type: 'number', attr: { precision: 2 } }, update: { type: 'number', attr: { precision: 2 } } } },
          { id: 'prosen_jht_employee', label: 'JHT Karyawan (%)', methods: { list: { transform: 'prosen' }, detail: { transform: 'prosen' }, create: { type: 'number', attr: { precision: 2 } }, update: { type: 'number', attr: { precision: 2 } } } }
        ]
